import { server } from "../../../config/server";
const url = "/report";

/**
 * 
 * @param {Object} options
 * @param {Number} options.branchId   - Branch ID whose report is to be calculated
 * @param {Date}   options.thisQuarter    - Base date to calculate the report Data
 * 
 */
async function branchCategorization(options) {
    const json = await server.get(
        `${url}/branch-categorization`,
        {
            params: options
        }
    );
    return json.data;
}

async function downloadBranchCategorization(data) {
    const json = await server.post(
        `${url}/branch-categorization/download`,
        JSON.stringify(data)
    );
    return json.data;
}

async function saveBranchCategorization(data) {
    const json = await server.post(
        `${url}/branch-categorization/save`,
        JSON.stringify(data)
    );
    return json.data;
}

export {
    branchCategorization,
    downloadBranchCategorization,
    saveBranchCategorization,
}