import { server } from "../../../config/server";
const url = "/report";

/**
 * 
 * @param {Object} options
 * @param {Number} options.branchId   - Branch ID whose report is to be calculated
 * @param {Number} options.frequency  - QUARTERLY, HALF_YEARLY, YEARLY (in months)
 * @param {String} options.status     - DRAFT, PENDING, UNAPPROVED, APPROVED (Risk Register status)
 * @param {String} options.mergeType  - avg, min, max (Merge Function Types)
 * @param {Date}   options.startDate    - Base date to calculate the report Data
 * 
 */
async function branchSummary(options) {
    const json = await server.get(
        `${url}/branch-summary`,
        {
            params: options
        }
    );
    return json.data;
}

async function downloadBranchSummary(data) {
    const json = await server.post(
        `${url}/branch-summary/download`,
        JSON.stringify(data)
    );
    return json.data;
}

async function saveBranchSummary(data) {
    const json = await server.post(
        `${url}/branch-summary/save`,
        JSON.stringify(data)
    );
    return json.data;
}

export {
    branchSummary,
    downloadBranchSummary,
    saveBranchSummary
}
