import { server } from "../../config/server";
const url = "/report";
const urlData = "/risk-register-all";
const log = "/audit-log";
const {
  branchSummary,
  downloadBranchSummary,
  saveBranchSummary
} = require("./api/branch_summary");
const { getRiskTriggers, getRiskTriggerData } = require("./api/risk_trigger");
const {
  branchCategorization,
  downloadBranchCategorization,
  saveBranchCategorization
} = require("./api/branch_categorization");
const { hoSummary } = require("./api/ho_summary");

async function getReportOutputs(options) {
  const json = await server.get(`${url}/report-outputs`, {
    params: options
  });
  return json.data;
}

async function getAllRiskRegister(options) {
  const json = await server.get(`${urlData}`, { params: options });
  return json.data;
}

function getAuditLog(callback) {
  server
    .get(`${log}`)
    .then(function(json) {
      // console.log(json.data);
      return callback(null, json.data);
    })
    .catch(function(err) {
      console.error("Error getting data from all risk Register!");
    });
}

async function saveGrading(data, options) {
  const json = await server.post(`${url}/grading`, {
    ...options,
    data: data
  });
  return json.data;
}

export {
  getAllRiskRegister,
  getAuditLog,
  getReportOutputs,
  // BRANCH SUMMARY
  branchSummary,
  downloadBranchSummary,
  saveBranchSummary,
  // BRANCH CATEGORIZATION
  branchCategorization,
  downloadBranchCategorization,
  saveBranchCategorization,
  // HO Summary
  hoSummary,
  //saving report
  saveGrading,
  //Risk Trigger
  getRiskTriggers,
  getRiskTriggerData
};
