import { server } from "../../../config/server";
const riskTrigger = "report/risk-trigger";
const riskTriggerData = "report/risk-trigger/data";

async function getRiskTriggerData(options) {
    return server.get(`${riskTriggerData}`, { params: options });
}

function getRiskTriggers(callback) {
    server
        .get(`${riskTrigger}`)
        .then(function (json) {
            callback(null, json.data);
        })
        .catch(function (err) {
            callback(err);
        });
}

export {
    getRiskTriggers,
    getRiskTriggerData,
}