const bs2ad = require('ad-bs-converter').bs2ad;
const ad2bs = require('ad-bs-converter').ad2bs;
const moment = require('moment');

const QUARTER_ONE = "/04/01";
const QUARTER_TWO = "/07/01";
const QUARTER_THREE = "/10/01";
const QUARTER_FOUR = "/01/01";

/**
 * 
 * @param {Object} ad - AD date object converted from bs date using ad-bs-converter 
 * @returns {String} Well formatted AD Date in YYYY-MM-DD
 */
const getFormattedDate = (ad) => {
    const year = ad.year;
    const month = ad.month;
    const day = ad.day;
    return moment(year + " " + month + " " + day, "YYYY MM DD").format('YYYY-MM-DD');
}

/**
 * @returns Quarter start date in AD of this running month
 */
const getStartQuarter = () => {
    const bs = ad2bs(moment().format("YYYY/MM/DD"));
    const year = bs.en.year;
    const quarter = getQuarter(bs);
    return getFormattedDate(bs2ad(year + quarter));
}

/**
 * 
 * @param {Date} ad - Well formatted AD Date in YYYY-MM-DD
 * @returns Returns in which quarter does the date lies
 */
const getQuarterByAD = (ad) => {
    const bs = ad2bs(moment(ad).format("YYYY/MM/DD"));
    return getQuarter(bs);
}

/**
 * 
 * @param {Object} bs - Object of BS date from ad-bs-converter
 * @returns Returns in which quarter does the date lies
 */
const getQuarter = (bs) => {
    const month = bs.en.month;
    if (month >= 10) {
        return QUARTER_THREE;
    } else if (month >= 7) {
        return QUARTER_TWO;
    } else if (month >= 4) {
        return QUARTER_ONE;
    } else {
        return QUARTER_FOUR;
    }
}

/**
 * 
 * @param {Date} ad Well formatted (YYYY-MM-DD) AD Date
 * @returns BS Date object in `en`
 */
const getBSDate = (ad) => {
    const bs = ad2bs(moment(ad).format("YYYY/MM/DD"));
    return bs.en;
}

export {
    getStartQuarter,
    getBSDate,
    getQuarterByAD,
    getFormattedDate,
    QUARTER_ONE,
    QUARTER_TWO,
    QUARTER_THREE,
    QUARTER_FOUR,
}