import React from "react";
import {   Bar } from "react-chartjs-2";
import {
  Card,
  CardBody,
  CardHeader,

} from "reactstrap";


export default class RiskRegisterData extends React.Component {
  state = {};

  render() {
    const {  barChartOptions, otherChart } = this.props;
    return (
      <Card>
        <CardHeader>Chart</CardHeader>
        <CardBody>
          {/* <Doughnut data={pieChart} options={barChartOptions} />
          <Radar data={pieChart} options={barChartOptions} /> */}
          {/* <Line data={otherChart} options={barChartOptions} /> */}
          <Bar data={otherChart} options={barChartOptions} />
        </CardBody>
      </Card>
    );
  }
}
